.send__button {
    background: transparent;
    color: #fff;
    border: 2px solid #fff;
    border-radius: 50%;
    padding: 10px;
}

.send__button:hover {
    background: #fff;
    color: var(--imp-back-color);
    font-weight: bold;
    transition: all 0.3s ease-in-out;
    transform: scale(1.1);
}

.form__container {
    padding: 10px 50px;
    /* border: 1px solid #fff; */
    margin-bottom: 50px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px #fff;
}

.Contact__map {
    height: 100%;
    width: 100%;
    padding: 20px;
    /* border: 1px solid #fff; */
    border-radius: 10px;
    margin-bottom: 50px;
    box-shadow: 0px 0px 10px #fff;
}